.filter-popup {
   min-width: 300px;
   min-height: 230px;
   max-height: calc(100vh - 60px);
   padding: 14px;
   overflow: auto;

   


   .title-container {
      justify-content: center;

      h1 {
         font-size: 14px;
         font-weight: 500;
         color: $clr8;
      }
   }

   hr {
      border-width: .5px;
   }

   .input-container {
      margin-bottom: 18px;
      margin-top: 10px;
      justify-content: center;
      align-items: center;

      .keyword-input {
         min-height: 34px;

         &:hover {
            background-color: darken($color: $clr11, $amount: 5);
         }

         &:focus {
            background-color: white !important;
         }

         // width: 80%;
         width: 100%;
         max-height: 30px;
         font-size: 14px;
         // color: $clr8;
         color: #5e6c84;
         background-color: $clr14;
         margin-bottom: -10px;
      }
   }

   h3 {
      margin-bottom: 18px;
      font-size: 12px;
      font-weight: 400;
   }

   h2 {
      font-weight: 500;
      font-size: 12px;
      color: #5e6c84;
      margin-bottom: 8px;
   }



   .filter-members {
      gap: 8px;

      .filter-member {
         gap: 6px;
         
         .filter-member-img-container {
            
            img {
               width: 30px;
               height: 30px;
               border-radius: 50%;
            }
         }
         h1{
            font-size: 16px;
            font-weight: 400;
         }
      }

   }
   .label-list {
      padding: 0 ;
      list-style: none;

      .label-list-item {
          min-width: 280px;
          min-height: 32px;
          margin-top: 5px;
          padding: 4px 10px;
          border-radius: 3px;
          display: flex;
          justify-content: space-between;
          cursor: pointer;

          span {
              color: #ffff;
              font-weight: 700;
              font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
          }

          &:hover {
              filter: brightness(90%);
          }
      }
  }


}